/** 常用色值 **/
/** 常用字体大小 **/
ul,
li {
  list-style: none;
}
.clearfix::after {
  content: '';
  clear: both;
  display: block;
  visibility: hidden;
}
.container {
  display: flex;
}
.container .nav-left {
  width: 15%;
  min-width: 180px;
  height: calc(100vh);
  background-color: #001529;
}
.container .main {
  flex: 1;
  height: calc(100vh);
  background-color: #f1f3f5;
  overflow: auto;
}
.container .content {
  position: relative;
  padding: 20px;
}
.content-wrap {
  background: #ffffff;
  border: 1px solid #e8e8e8;
  margin-top: -3px;
}
.content-wrap .ant-table-wrapper {
  margin-left: -1px;
  margin-right: -2px;
}
.detail-items {
  margin-left: 90px;
  padding: 25px 50px 25px 0;
  border-bottom: 1px solid #e5e5e5;
}
.detail-items:last-child {
  border-bottom: none;
}
.detail-items .item-title {
  margin: 20px 0;
  font-size: 20px;
  color: #bebebe;
}
.detail-items .detail-form li {
  margin: 20px 0;
  line-height: 20px;
  font-size: 15px;
  color: #333;
}
.detail-items .detail-form li::after {
  content: '';
  clear: both;
  display: block;
  visibility: hidden;
}
.detail-items .detail-form-left {
  float: left;
  width: 164px;
  text-align: right;
  color: #999;
}
.detail-items .detail-form-content {
  padding-left: 194px;
}
.order-map {
  height: 450px;
  margin: 25px auto 0;
}
